import React from 'react';
import { cn } from '@bem-react/classname';
import './ProfilePicture.scss';

const ProfilePicture = ({ src }) => {
  const classItem = cn('ProfilePicture');

  return (
    <picture className={classItem()}>
      <source srcset={src.webp} type="image/webp"></source>
      <img src={src.jpg} className={classItem('Image')} alt="Dmitriy Khanin" loading="lazy"/>
    </picture>
  )
};

export default ProfilePicture;